<template>
  <nav :class="navbarClass" class="border-gray-200 px-4 py-4 fixed top-0 left-0 w-full z-50">
    <div class="container flex flex-wrap items-center justify-between mx-auto">
      <a href="#" class="flex items-center">
        <img src="../assets/logo.png" class="h-12 sm:h-16 md:h-20" alt="Kiki's Servicio Logo" />
        <span class="self-center text-lg sm:text-xl md:text-2xl font-semibold whitespace-nowrap text-white">Kiki's {{ variable }}</span>
      </a>
      <div class="flex md:order-2">
        <!-- Carrito de compras -->
        <router-link to="/refaccionaria/ventas" class="flex items-center text-white">
          <svg class="w-6 h-6 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13l-2 5h12l2-5m-4 5a1 1 0 100 2 1 1 0 000-2m-8 0a1 1 0 100 2 1 1 0 000-2"></path>
          </svg>
          <span>({{ (cartTotalQuantity) }}) artículos</span>
        </router-link>
        <button data-collapse-toggle="navbar-default" type="button"
          class="inline-flex items-center p-2 text-sm text-gray-500 rounded-lg md:hidden hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-200" aria-controls="navbar-default" aria-expanded="false">
          <span class="sr-only">Open main menu</span>
          <svg class="w-6 h-6" aria-hidden="true" fill="currentColor" viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg">
            <path fill-rule="evenodd"
              d="M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 15a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1z"
              clip-rule="evenodd"></path>
          </svg>
        </button>
      </div>
      <div class="hidden w-full md:flex md:w-auto md:order-1" id="navbar-default">
        <ul class="flex flex-col md:flex-row mt-4 border border-gray-100 rounded-lg md:space-x-8 md:mt-0 md:border-0 items-center">
          <li>
            <router-link to="/" class="nav-link block py-2 pl-3 pr-4 text-white rounded md:bg-transparent md:p-0 text-lg"
              aria-current="page">Inicio</router-link>
          </li>
          <li>
            <router-link to="/services" class="nav-link block py-2 pl-3 pr-4 text-white rounded md:bg-transparent md:p-0 text-lg">Servicios</router-link>
          </li>
          <li>
            <router-link to="/contact" class="nav-link block py-2 pl-3 pr-4 text-white rounded md:bg-transparent md:p-0 text-lg">Contacto</router-link>
          </li>
          <li>
            <router-link to="/refaccionaria" class="nav-link block py-2 px-4 text-white bg-red-600 rounded-md hover:bg-red-700 text-lg">Tenemos Refaccionaria!</router-link>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>
<script>
import { mapState, mapActions, mapGetters } from 'vuex';
import { onMounted, onBeforeUnmount } from 'vue';

export default {
  name: 'NavbarComponent',
  computed: {
    ...mapState(['variable']),
    ...mapGetters(['cartTotalQuantity']), // Para obtener la cantidad de artículos en el carrito
    navbarClass() {
      return this.variable === 'Refaccionaria' ? 'bg-gray-800' : 'bg-blue-800';
    }
  },
  methods: {
    ...mapActions(['updateVariable']),
    toggleNavbar() {
      const navbar = document.getElementById('navbar-default');
      navbar.classList.toggle('hidden');
    }
  },
  setup() {
    const toggleNavbar = () => {
      const navbar = document.getElementById('navbar-default');
      navbar.classList.toggle('hidden');
    };

    onMounted(() => {
      const toggleButton = document.querySelector('[data-collapse-toggle="navbar-default"]');
      toggleButton.addEventListener('click', toggleNavbar);

      const navLinks = document.querySelectorAll('.nav-link');
      navLinks.forEach(link => {
        link.addEventListener('click', toggleNavbar);
      });
    });

    onBeforeUnmount(() => {
      const toggleButton = document.querySelector('[data-collapse-toggle="navbar-default"]');
      toggleButton.removeEventListener('click', toggleNavbar);

      const navLinks = document.querySelectorAll('.nav-link');
      navLinks.forEach(link => {
        link.removeEventListener('click', toggleNavbar);
      });
    });
  }
};
</script>
