<template>
  <div class="products container mx-auto px-4 py-16">
    <h1 class="text-3xl font-bold text-center mb-8">Catálogo de Productos</h1>
    <!-- Buscador -->
    <div class="flex justify-center mb-8">
      <input v-model="searchQuery" type="text" placeholder="Buscar productos..."
        class="w-full md:w-1/2 p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500" />
    </div>
    <!-- Lista de Productos -->
    <div class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
      <div v-for="product in filteredProducts" :key="product.id" class="product-card bg-white rounded-lg shadow-md p-6">
        <img :src="product.image" alt="Product Image" class="product-image w-full h-48 object-cover mb-4 rounded-lg" />
        <h2 class="product-title text-xl font-semibold mb-2">{{ product.name }}</h2>
        <p class="product-description text-gray-600 mb-4">{{ product.description }}</p>
        <div class="flex justify-between items-center">
          <span class="product-price text-lg font-bold text-blue-600">${{ product.price }}</span>
          <button @click="handleAddToCart(product)"
            class="add-to-cart-button bg-blue-600 text-white font-semibold px-4 py-2 rounded-lg hover:bg-blue-700 focus:ring-2 focus:ring-blue-500">
            Agregar al Carrito
          </button>
        </div>
      </div>
    </div>

    <!-- Diálogo de Confirmación -->
    <DialogComponent
      :isVisible="isDialogVisible"
      title="Producto Agregado"
      status="success" 
      @close="isDialogVisible = false"
      @confirm="handleDialogConfirm"
      @cancel="handleDialogCancel"
    >
      <p class="text-center">El producto ha sido agregado al carrito con éxito.</p>
      <br>
      <p class="text-center">{{ lastAddedProduct ? `Producto: ${lastAddedProduct.name}` : '' }}</p>
      <br>
      <p class="text-center">En caso de querer eliminar el producto o reducir la cantidad, dirijase al carrito de compras para poder modificar la accion</p>
    </DialogComponent>

    <!-- Diálogo de Cancelación -->
    <DialogComponent
      :isVisible="isCancelDialogVisible"
      title="Producto No Agregado"
      status="error"
      @close="isCancelDialogVisible = false"
      @confirm="isCancelDialogVisible = false"
    >
      <p>No se pudo agregar el producto al carrito.</p>
    </DialogComponent>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import DialogComponent from '../../components/Dialog.vue'; // Asegúrate de que la ruta sea correcta

export default {
  name: "ProductsPage",
  components: {
    DialogComponent
  },
  data() {
    return {
      searchQuery: "", // Para almacenar el término de búsqueda
      products: [
        {
          id: 1,
          name: "Pastillas de Freno",
          description: "Descripción del Producto 1",
          price: 10.99,
          image: "path/to/product1.jpg",
        },
        {
          id: 2,
          name: "Bujias",
          description: "Descripción del Producto 2",
          price: 19.99,
          image: "path/to/product2.jpg",
        },
        {
          id: 3,
          name: "Solenoide",
          description: "Descripción del Producto 3",
          price: 19.99,
          image: "path/to/product3.jpg",
        }
        // Agrega más productos aquí
      ],
      isDialogVisible: false, // Estado del diálogo de éxito
      isCancelDialogVisible: false, // Estado del diálogo de error
      lastAddedProduct: null, // Producto que se intentó agregar
      previousCartState: [] // Estado previo del carrito
    };
  },
  computed: {
    filteredProducts() {
      return this.products.filter(product => {
        const searchTerm = this.searchQuery.toLowerCase();
        return (
          product.name.toLowerCase().includes(searchTerm) ||
          product.description.toLowerCase().includes(searchTerm)
        );
      });
    },
    // Computar el carrito actual desde Vuex
    cartItems() {
      return this.$store.getters.cartItems;
    }
  },
  methods: {
    ...mapActions(['addToCart', 'removeProductFromCart', 'updateCartQuantity']),
    handleAddToCart(product) {
      // Guardar el estado actual del carrito
      this.previousCartState = [...this.cartItems];

      // Verificar si el producto ya está en el carrito
      const existingProduct = this.cartItems.find(item => item.id === product.id);

      if (existingProduct) {
        // Si el producto ya está en el carrito, solo se actualiza la cantidad
        this.$store.dispatch('updateCartQuantity', { id: product.id, quantity: existingProduct.quantity + 1 })
          .then(() => {
            this.isDialogVisible = true; // Mostrar diálogo de éxito
            this.lastAddedProduct = { ...existingProduct, quantity: existingProduct.quantity + 1 }; // Actualizar producto
          })
          .catch(() => {
            this.isCancelDialogVisible = true; // Mostrar diálogo de error si falla
          });
      } else {
        // Si el producto no está en el carrito, agregarlo
        this.$store.dispatch('addToCart', product)
          .then(() => {
            this.isDialogVisible = true; // Mostrar diálogo de éxito
            this.lastAddedProduct = product; // Guardar el último producto agregado
          })
          .catch(() => {
            this.isCancelDialogVisible = true; // Mostrar diálogo de error si falla
          });
      }
    },
    handleDialogConfirm() {
      // Lógica adicional para confirmar si es necesario
      console.log('Acción confirmada');
    },
    handleDialogCancel() {
      if (this.lastAddedProduct) {
        // Revertir el estado del carrito al anterior
        this.$store.dispatch('restoreCartState', this.previousCartState)
          .then(() => {
            this.isCancelDialogVisible = false; // Ocultar diálogo de cancelación
          })
          .catch(() => {
            // Manejo de errores si la restauración falla
            console.error('Error al revertir el estado del carrito');
          });
      }
    }
  },
};
</script>

<style scoped>
.products {
  max-width: 1200px;
}
</style>
