<template>
  <!-- Services Section -->
  <section class="container mx-auto px-4 py-16">
    <h2 class="text-3xl font-bold mb-8 text-center">Nuestros Servicios</h2>
    <br>
    <div class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
      <!-- Iterar sobre los servicios -->
      <div v-for="(service, index) in services" :key="index"
        class="p-6 bg-white rounded-lg shadow-lg border-2 border-blue-600">
        <h3 class="text-2xl font-bold mb-4 text-blue-700">{{ service.title }}</h3>
        <p class="text-gray-700">{{ service.description }}</p>
      </div>
    </div>
  </section>
</template>


<script>
import { mapState, mapActions } from 'vuex';

export default {
  name: 'ServicesPage',
  computed: {
    ...mapState(['variable']),
  },
  mounted() {
    this.changeVariable();
  },
  methods: {
    ...mapActions(['updateVariable']),
    changeVariable() {
      this.updateVariable('Servicio');
    },
  },
  data() {
    return {
      services: [
        { title: 'Cambio de Aceite', description: 'Mantén tu motor funcionando sin problemas con nuestro servicio de cambio de aceite de alta calidad.' },
        { title: 'Reparación de Frenos', description: 'Asegúrate de que tu vehículo esté seguro con nuestro servicio de reparación de frenos de primera calidad.' },
        { title: 'Alineación y Balanceo', description: 'Mejora el manejo de tu vehículo con nuestro servicio de alineación y balanceo.' },
      ],
    };
  },
};
</script>