import { createRouter, createWebHashHistory } from 'vue-router';
import HomePage from '../views/Home.vue';
import ServicesPage from '../views/Services.vue';
import ContactPage from '../views/Contact.vue';
import AppointmentsPage from '../views/Appointments.vue';
import Refaccionaria from '../views/Refaccionaria/RefaccionariaHome.vue';
import Products from '../views/Refaccionaria/Products.vue';
import Ventas from '../views/Refaccionaria/Ventas.vue';

import NotFound from '../views/NotFound.vue'; // Importar el componente 404

const routes = [
  { path: '/', name: 'Home', component: HomePage },
  { path: '/services', name: 'Services', component: ServicesPage },
  { path: '/contact', name: 'Contact', component: ContactPage },
  { path: '/appointments', name: 'Appointments', component: AppointmentsPage },
  { path: '/refaccionaria', name: 'Refaccionaria', component: Refaccionaria },
  { path: '/refaccionaria/Products', name: 'Products', component: Products },
  { path: '/refaccionaria/Ventas', name: 'Ventas', component: Ventas },
  { path: '/:pathMatch(.*)*', name: 'NotFound', component: NotFound }, // Ruta para manejar 404
];

const router = createRouter({
  history: createWebHashHistory(), // Puedes usar createWebHistory si no usas GitHub Pages
  routes,
});

export default router;
