<template>
  <section class="container mx-auto px-4 py-16">
    <h2 class="text-4xl font-bold mb-8 text-center">Agenda tu Cita</h2>
    <form @submit.prevent="submitForm" class="bg-white p-8 rounded-lg shadow-lg max-w-lg mx-auto">
      <div class="mb-6">
        <label for="name" class="block text-sm font-medium text-gray-900">Nombre Completo</label>
        <input type="text" id="name" v-model="name" class="block w-full mt-1 p-2.5 border border-gray-300 rounded-lg"
          required />
      </div>
      <div class="mb-6">
        <label for="email" class="block text-sm font-medium text-gray-900">Correo Electrónico</label>
        <input type="email" id="email" v-model="email" class="block w-full mt-1 p-2.5 border border-gray-300 rounded-lg"
          required />
      </div>
      <div class="mb-6">
        <label for="date" class="block text-sm font-medium text-gray-900">Fecha de la Cita</label>
        <input type="date" id="date" v-model="date" class="block w-full mt-1 p-2.5 border border-gray-300 rounded-lg"
          required />
      </div>
      <div class="mb-6">
        <label for="service" class="block text-sm font-medium text-gray-900">Servicio</label>
        <select id="service" v-model="service" class="block w-full mt-1 p-2.5 border border-gray-300 rounded-lg">
          <option value="Cambio de Aceite">Cambio de Aceite</option>
          <option value="Reparación de Frenos">Reparación de Frenos</option>
          <option value="Alineación y Balanceo">Alineación y Balanceo</option>
        </select>
      </div>
      <button type="submit"
        class="w-full text-white bg-blue-700 hover:bg-blue-800 font-medium rounded-lg text-sm px-5 py-2.5 text-center">Enviar</button>
    </form>
  </section>
</template>

<script>
import emailjs from 'emailjs-com';
import { mapState, mapActions } from 'vuex';

export default {
  name: 'AppointmentsPage',
  data() {
    return {
      name: '',
      email: '',
      date: '',
      service: '',
    };
  },
  computed: {
    ...mapState(['variable']),
  },
  mounted() {
    this.changeVariable();
  },
  methods: {
    ...mapActions(['updateVariable']),
    changeVariable() {
      this.updateVariable('Servicio'); // Modifica la variable global
    },
    async submitForm() {
      const templateParams = {
        name: this.name,
        email: this.email,
        date: this.date,
        service: this.service,
      };

      try {
        await emailjs.send('service_b5m3a2x', 'template_f0vpuot', templateParams, '72UWt7VN90Evylsl_');
        alert('Cita agendada con éxito');
        this.name = '';
        this.email = '';
        this.date = '';
        this.service = '';
      } catch (error) {
        console.error('Error al enviar el correo:', error);
        alert('Error al enviar la cita');
      }
    },
  },
};
</script>
