<template>
    <div class="max-w-md mx-auto bg-white shadow-lg rounded-lg overflow-hidden mt-6 p-6">
      <h2 class="text-2xl font-semibold text-gray-800 mb-4">Carrito de Compras</h2>
      <ul v-if="cartItems && cartItems.length" class="space-y-4">
        <li 
          v-for="item in cartItems" 
          :key="item.id" 
          class="flex items-center justify-between border-b pb-2">
          <div class="flex items-center space-x-4">
            <div>
              <p class="text-lg font-medium text-gray-700">{{ item.name }}</p>
              <p class="text-sm text-gray-600">{{ item.quantity }} x ${{ item.price }} = ${{ item.quantity * item.price }}</p>
            </div>
            <input 
              type="number" 
              v-model.number="item.quantity"
              @change="updateQuantity(item.id, item.quantity)"
              min="1"
              class="w-20 text-center border border-gray-300 rounded-md"
            />
          </div>
          <button 
            @click="removeFromCart(item.id)" 
            class="text-red-500 hover:text-red-700 text-sm font-semibold"
          >
            Eliminar
          </button>
        </li>
      </ul>
      <p v-else class="text-center text-gray-500">Tu carrito está vacío.</p>
      <div class="mt-4">
        <p class="text-xl font-semibold text-gray-800">Total: ${{ cartTotalPrice }}</p>
        <button 
          @click="clearCart" 
          class="mt-4 w-full bg-red-600 text-white py-2 rounded-md hover:bg-red-700 transition-colors"
        >
          Vaciar Carrito
        </button>
      </div>
    </div>
  </template>
  
  <script>
  import { mapGetters, mapActions } from 'vuex';
  
  export default {
    name: 'CartComponent',
    computed: {
      ...mapGetters(['cartItems', 'cartTotalPrice']),
    },
    methods: {
      ...mapActions(['removeProductFromCart', 'clearCart', 'updateCartQuantity']),
      removeFromCart(productId) {
        this.removeProductFromCart(productId);
      },
      updateQuantity(productId, newQuantity) {
        // Asegúrate de que la cantidad sea mayor a 0
        if (newQuantity > 0) {
          this.updateCartQuantity({ id: productId, quantity: newQuantity });
        }
      }
    }
  };
  </script>
  
  <style scoped>
  /* Añade estilos adicionales si es necesario */
  </style>
  