<template>
    <div class="appointments">
      <h2>Programar una Cita</h2>
      <form @submit.prevent="bookAppointment">
        <div>
          <label for="name">Nombre:</label>
          <input type="text" id="name" v-model="name" required />
        </div>
        <div>
          <label for="date">Fecha:</label>
          <input type="date" id="date" v-model="date" required />
        </div>
        <div>
          <label for="service">Servicio:</label>
          <select id="service" v-model="service" required>
            <option value="Diagnóstico">Diagnóstico</option>
            <option value="Cambio de Aceite">Cambio de Aceite</option>
            <option value="Reparación de Frenos">Reparación de Frenos</option>
            <option value="Alineación">Alineación</option>
          </select>
        </div>
        <button type="submit">Agendar Cita</button>
      </form>
    </div>
  </template>
  
  <script>
  export default {
    name: 'AppointmentsPage',
    data() {
      return {
        name: '',
        date: '',
        service: '',
      };
    },
    methods: {
      bookAppointment() {
        alert(`Cita agendada para ${this.name} el ${this.date} para ${this.service}`);
      },
    },
  };
  </script>
  
  <style scoped>
  .appointments {
    color: #d9d9d9;
  }
  
  form {
    display: flex;
    flex-direction: column;
    max-width: 400px;
    margin: auto;
  }
  
  div {
    margin-bottom: 15px;
  }
  
  label {
    margin-bottom: 5px;
  }
  
  input, select {
    padding: 10px;
    font-size: 1em;
    border-radius: 5px;
    border: none;
  }
  
  button {
    padding: 10px;
    background-color: #004488;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #003366;
  }
  </style>
  