<template>
    <div v-if="isVisible" class="fixed inset-0 flex items-center justify-center z-50 bg-black bg-opacity-50">
      <div class="bg-white p-6 rounded-lg shadow-lg max-w-lg w-full">
        <div class="flex justify-between items-center mb-4">
          <h2 class="text-2xl font-bold">{{ title }}</h2>
          <button @click="closeDialog" class="text-gray-500 hover:text-gray-800 text-2xl">&times;</button>
        </div>
        <div class="flex flex-col items-center mb-4">
          <!-- Ícono de estado -->
          <div v-if="status === 'success'" class="text-green-500 text-6xl mb-4">
            <svg xmlns="http://www.w3.org/2000/svg" class="w-16 h-16" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M5 13l4 4L19 7" />
            </svg>
          </div>
          <div v-else-if="status === 'error'" class="text-red-500 text-6xl mb-4">
            <svg xmlns="http://www.w3.org/2000/svg" class="w-16 h-16" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" />
            </svg>
          </div>
          <div class="mb-4">
            <!-- Slot para contenido dinámico -->
            <slot></slot>
          </div>
          <div class="flex justify-end space-x-4">
            <button @click="handleCancel" class="px-4 py-2 bg-gray-300 text-gray-700 rounded-lg hover:bg-gray-400">Cancelar</button>
            <button @click="confirmAction" class="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700">Aceptar</button>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'DialogComponent',
    props: {
      isVisible: {
        type: Boolean,
        default: false
      },
      title: {
        type: String,
        default: 'Título del Diálogo'
      },
      status: {
        type: String,
        default: '' // 'success' or 'error'
      }
    },
    methods: {
      closeDialog() {
        this.$emit('close');
      },
      confirmAction() {
        this.$emit('confirm');
        this.closeDialog();
      },
      handleCancel() {
        this.$emit('cancel');
        this.closeDialog();
      }
    }
  };
  </script>
  
  <style scoped>
  .bg-white {
    background-color: #ffffff;
  }
  .text-gray-500 {
    color: #6b7280;
  }
  .text-gray-700 {
    color: #4b5563;
  }
  .text-gray-800 {
    color: #1f2937;
  }
  .bg-gray-300 {
    background-color: #d1d5db;
  }
  .bg-gray-400 {
    background-color: #9ca3af;
  }
  .bg-blue-600 {
    background-color: #2563eb;
  }
  .bg-blue-700 {
    background-color: #1d4ed8;
  }
  .text-green-500 {
    color: #10b981;
  }
  .text-red-500 {
    color: #ef4444;
  }
  </style>
  